<template>
 <v-card id="card" class="pa-5">
   <v-row>
     <v-col>
       <v-row justify="center" align="center">
         <v-col class="d-flex justify-center"><span class="text-h5">{{submission.username}}</span></v-col>
       </v-row>
     </v-col>
     <v-divider vertical></v-divider>
     <v-col>
       <v-row justify="center" align="center">
         <v-col class="d-flex justify-center"><span class="text-h5">{{score}}/{{possible_points}} - {{score_percent}}%</span></v-col>
       </v-row>
     </v-col>
     <v-divider vertical></v-divider>
     <v-col>

     </v-col>
   </v-row>
 </v-card>
</template>

<script>
export default {
  props: ['submission'],
  name: "UserSubmission",
  data: function() {
    return {
      possible_points: 0,
      score: 0
    }
  },
  beforeMount() {
    let count = 0;
    for (const s of this.submission.score) {
      count++;
      if(s.correct) this.score++;
    }
    this.possible_points = count;
  },
  computed: {
    score_percent: function() {
      return (this.score / this.possible_points).toFixed(2) * 100;
    }
  }
}
</script>

<style scoped>
#card {
  border: #984447 solid 2px !important;
}
</style>