  border: #984447 solid 2px !important;
}
#content_textarea {
  width: 100%;
  height: 100%;
}
#content_textarea::-webkit-scrollbar {
  width: 5px;
}
#content_textarea::-webkit-scrollbar-track {
  background: white;
}
#content_textarea::-webkit-scrollbar-thumb {
  background: #456990;
}
#content_textarea::-webkit-scrollbar-thumb:hover {
  background: rgba(61, 125, 193, 0.8);
}
