<template>
  <v-row justify="center">
    <v-menu bottom min-width="200px" rounded offset-y>
      <template v-slot:activator="{on}">
        <v-btn icon x-large v-on="on">
          <v-avatar color="secondary" size="48">
            <span class="white--text text-h5">ME</span>
          </v-avatar>
        </v-btn>
      </template>
      <v-card>
        <v-list-item-content class="justify-center">
          <div class="mx-auto text-center">
            <h3 class="my-1">{{ email }}</h3>
            <v-divider class="my-3"></v-divider>

            <v-row dense align="center">
              <v-col>
                <v-icon v-if="dark_mode">mdi-white-balance-sunny</v-icon>
                <v-icon v-else>mdi-brightness-3</v-icon>
              </v-col>
              <v-col>
                <v-layout justify-center>
                  <v-switch v-model="dark_mode" inset @click="toggleDarkMode"></v-switch>
                </v-layout>

              </v-col>
            </v-row>

            <v-divider class="my-3"></v-divider>
            <v-btn depressed rounded text @click.stop="SendPasswordReset">
              Reset Password
            </v-btn>
            <v-divider class="my-3"></v-divider>
            <v-btn color="red" @click.stop="Logout" depressed rounded text>
              Logout
            </v-btn>
          </div>
        </v-list-item-content>
      </v-card>
    </v-menu>
  </v-row>
</template>

<script>
import firebase from 'firebase';

export default {
  props: ['email'],
  name: 'Avatar',
  data: function () {
    return {
      dark_mode: false,
    }
  },
  methods: {
    Logout() {
      firebase.auth().signOut().then(() => console.log("signed out")).catch(err => alert(err.message));
    },
    SendPasswordReset() {
      firebase.auth().sendPasswordResetEmail(this.email).then(() => {
        // On success
        this.$emit('notification_message', "Email Sent! Check your email to reset your password.");
      }).catch(err => console.error(err));
    },
    toggleDarkMode() {
      this.$emit('toggleDarkMode');
    }
  }
}
</script>