<template>
  <div class="dashboard">
    <v-app-bar fixed app color="primary" dark>
      <v-spacer></v-spacer>
      <v-toolbar-title id="title" class="white--text">Teacher's Toolbox</v-toolbar-title>
      <v-spacer></v-spacer>
      <avatar id="avatar" class="float-right" :email="email" @notification_message="notification"
              @toggleDarkMode="toggleDarkMode"></avatar>
    </v-app-bar>
    <v-main>
      <v-container class="mt-5">
        <h1 class="d-inline-block">My Tools</h1>
        <hr>
        <br>
        <div id="tool_cards" ref="tool_cards">
          <v-row>
            <!-- User Made Tools go here -->
            <v-col align="center" cols="12" xs="12" sm="6" md="6" lg="4" xl="2" v-for="(user_tool, index) in user_tools" :key="index">
              <component :is="user_tool.card_type" :user="user" :tool="user_tool.tool" :doc_Id="user_tool.id"></component>
            </v-col>
            <!-- Add New Tool Button here -->
            <v-col align="center" cols="12" xs="12" sm="6" md="6" lg="4" xl="2">
              <new-tool :user="user" :loading="loading" @notification_message="notification" @reload_dashboard="ReloadDashboard"></new-tool>
            </v-col>
          </v-row>
        </div>
        <v-snackbar v-model="notification_bar">
          {{ notification_message }}
          <template v-slot:action="{attrs}">
            <v-btn color="red" clas="white--text" text v-bind="attrs" @click="notification_bar = false">Ignore</v-btn>
          </template>
        </v-snackbar>
      </v-container>
    </v-main>
  </div>
</template>

<script>
import Avatar from '../components/Avatar.vue';
import NewTool from '../components/NewTool.vue';
import TimelineToolCard from "../components/tools/TimelineToolBuilder/TimelineToolCard";
import firebase from 'firebase';

export default {
  components: {
    NewTool,
    Avatar,
    TimelineToolCard
  },
  data: function () {
    return {
      loading: true,
      user: null,
      uid: "",
      name: "",
      email: "",
      user_tools: [],
      dark_mode: false,
      notification_bar: false,
      notification_message: "No notifications."
    }
  },
  beforeMount: function () {
    firebase.auth().onAuthStateChanged(async (user) => {
      if (user) {
        this.user = user;
        this.email = user.email;
        this.name = user.email.split('@')[0];
        this.uid = user.uid;
      }
      await this.ReloadDashboard();
      this.loading = false;
    });

  },
  methods: {
    toggleDarkMode() {
      this.dark_mode = !this.dark_mode;
      this.$vuetify.theme.dark = this.dark_mode;
    },
    notification(msg) {
      this.notification_message = msg;
      this.notification_bar = true;
    },
    async ReloadDashboard() {
      // Grab user associated data and populate user_tools after authentication
      this.user_tools = [];
      await firebase.firestore().collection('users').doc(this.uid.toString()).collection('tools').get()
          .then((query) => {
            query.forEach((doc) => {
              this.user_tools.push({ id: doc.id, tool: doc.data(), card_type: this.getType(doc.data().type) });
            });
          })
          .catch(err => console.error(err));
    },
    getType(typeStr) {
      switch (typeStr) {
        case "timeline":
          return TimelineToolCard;
      }
    },
    resizeWindow() {
      let cards = this.$refs.tool_cards;
      cards.style.height = ((innerHeight - cards.offsetTop) - 65) + 'px';
    }
  },
  mounted() {
    setTimeout(() => {
      this.resizeWindow();
    }, 1000);
  },
  created() {
    window.addEventListener("resize", this.resizeWindow);
  },
  destroyed() {
    window.removeEventListener("resize", this.resizeWindow);
  },
}
</script>
<style scoped>
#tool_cards {
  min-height: 325px;
  overflow-y: auto;
  overflow-x: hidden;
}
#tool_cards::-webkit-scrollbar {
  height: 10px;
}
#tool_cards::-webkit-scrollbar-track {
  background: white;
}
#tool_cards::-webkit-scrollbar-thumb {
  background: #456990;
  border-radius: 50px;
}
#tool_cards::-webkit-scrollbar-thumb:hover {
  background: rgba(61, 125, 193, 0.8);
}
#avatar {
  position: absolute;
  right: 2%;
}
#title {
  font-family: 'Permanent Marker', cursive;
  font-size: 50px;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  user-select: none;
}
</style>