<template>
  <v-card @click.stop="OpenTimelineToolBuilder" elevation="5" outlined color="white" id="timeline_tool_card" max-width="250"
          :style="card_style">
    <v-img src="@/assets/box-timeline-transparent1.png"></v-img>
    <v-divider></v-divider>
    <v-card-title id="title">{{ tool.title }}</v-card-title>
    <timeline-tool-builder :vis="timeline_tool_builder"
                           :user="user"
                           :timeline_details="{isNew: false, timeline: tool, doc_Id: doc_Id}"
                           @close_timeline_builder="timeline_tool_builder = false"
                           @reload_dashboard="ReloadDashboard"
    ></timeline-tool-builder>
  </v-card>
</template>

<script>
import TimelineToolBuilder from "@/components/tools/TimelineToolBuilder/TimelineToolBuilder";

export default {
  name: "TimelineToolCard.vue",
  props: ['user', 'tool', 'doc_Id'],
  data: function() {
    return {
      timeline_tool_builder: false,
    }
  },
  components: {
    TimelineToolBuilder
  },
  methods: {
    OpenTimelineToolBuilder() {
      this.timeline_tool_builder = true;
    },
    ReloadDashboard() {
      this.$emit('reload_dashboard');
    }
  },
  computed: {
    card_style: function () {
      return {
        backgroundColor: `${this.$vuetify.theme.currentTheme.accent}`
      }
    }
  }
}
</script>

<style scoped>
#title {
  word-break: normal;
}
#timeline_tool_card {
  height: 300px;
  border: 1px solid #984447;
}
</style>