<template>
  <v-dialog id="timeline_tool_builder" v-model="vis" max-width="500" fullscreen hide-overlay persistent
            transition="dialog-bottom-transition">
    <v-card>
      <v-toolbar dark color="secondary">
        <v-btn icon dark @click="CloseTimelineToolBuilder">
          <v-icon>mdi-close</v-icon>
        </v-btn>
        <v-toolbar-title>Timeline Tool Builder</v-toolbar-title>
        <v-spacer></v-spacer>
        <v-toolbar-items>
          <v-btn v-if="timeline_details.isNew" text @click="SaveTool">Save</v-btn>
          <v-btn v-else text @click="UpdateTool">Update</v-btn>
        </v-toolbar-items>
      </v-toolbar>

      <!-- Timeline Builder -->
      <v-container>
        <v-tabs v-model="tab">
          <v-tab>Contents</v-tab>
          <v-tab>Tool Options</v-tab>
          <v-tab>Submissions</v-tab>
        </v-tabs>
        <v-tabs-items v-model="tab">
          <v-tab-item class="pa-5">
            <v-card flat>
              <v-list-item>
                <v-list-item-content>
                  <v-text-field color="secondary" v-model="timeline.title" filled label="Tool Title" clearable height="60px" style="font-size: 30px;"></v-text-field>
                </v-list-item-content>
              </v-list-item>
            </v-card>
            <v-card flat class="mx-5">
              <v-card dense color="secondary">
                <v-row>
                  <v-col class="d-flex justify-center white--text h5">Date (Year, Month, Day)</v-col>
                  <v-col class="d-flex justify-center white--text h5">Card Contents</v-col>
                  <v-col class="d-flex justify-center white--text h5">Card Options</v-col>
                </v-row>
              </v-card>
              <br>
              <!-- Contents -->
              <div v-for="(point, index) in timeline.points" :key="index">
                <timeline-point class="point" :point_data="point" @remove-point="RemovePoint" @notification_message="notification"></timeline-point>
              </div>
              <br>
              <v-card>
                <v-btn @click.stop="AddNewPoint" block color="secondary">ADD POINT</v-btn>
              </v-card>
            </v-card>
          </v-tab-item>
          <v-tab-item>
            <v-card flat>
              <!-- Options -->
              <v-container>
                <h2>Tool Link</h2>
                <hr class="divider">
                <div class="pa-5">
                  <v-text-field id="timeline_url" readonly type="text" v-model="timeline.url" outlined hide-details placeholder="Save tool to generate link..." prepend-icon="mdi-content-copy" @click:prepend="CopyURL"></v-text-field>
                </div>
                <h2>Submission Options</h2>
                <hr class="divider">
                <div>
                  <v-radio-group v-model="timeline.options.allowSubmissions" row>
                    <v-radio label="Enable" :value="true"></v-radio>
                    <v-radio label="Disable" :value="false"></v-radio>
                  </v-radio-group>
                </div>
                <v-container class="px-5">
                  <div>
                    <h4>After Submission:</h4>
                    <v-radio-group mandatory v-model="timeline.options.afterSubmission" :disabled="!timeline.options.allowSubmissions">
                      <v-radio label="Do nothing." value="none"></v-radio>
                      <v-radio label="Show wrong answers along with correct answers." value="solution"></v-radio>
                      <v-radio label="Show wrong answers only." value="score"></v-radio>
                    </v-radio-group>
                  </div>
                </v-container>
              </v-container>
            </v-card>
          </v-tab-item>
          <!-- Submissions -->
          <v-tab-item>
            <v-card flat>
              <v-container>
                <h2>Submissions</h2>
                <hr class="divider">
                <v-card dense color="secondary" class="ma-5">
                  <v-row>
                    <v-col class="d-flex justify-center white--text h5">Username</v-col>
                    <v-col class="d-flex justify-center white--text h5">Score</v-col>
                    <v-col class="d-flex justify-center white--text h5">Options</v-col>
                  </v-row>
                </v-card>
                <div v-if="submissions.length === 0">
                  <v-card>
                    <h3>No submissions yet...</h3>
                  </v-card>
                </div>
                <div v-for="(sub, index) in submissions" :key="index">
                  <user-submission :submission="sub" :key="index" class="ma-5"></user-submission>
                </div>
              </v-container>
            </v-card>
          </v-tab-item>
        </v-tabs-items>
        <v-overlay :value="overlay">
          <v-progress-circular size="64"></v-progress-circular>
        </v-overlay>
      </v-container>
    </v-card>
    <v-snackbar v-model="notification_bar" style="z-index: 10">
      {{ notification_message }}
      <template v-slot:action="{attrs}">
        <v-btn color="red" clas="white--text" text v-bind="attrs" @click="notification_bar = false">Ignore</v-btn>
      </template>
    </v-snackbar>
  </v-dialog>
</template>

<script>
import TimelinePoint from './TimelinePoint.vue';
import firebase from "firebase";
import UserSubmission from "@/components/tools/TimelineToolBuilder/UserSubmission";
import { uuid } from 'vue-uuid';

class Point {
  constructor(year, month, day, content,
              defaultInPlace, isImpostor, calendarEra, media) {
    this.year = year;
    this.month = month;
    this.day = day;
    this.content = content;
    this.defaultInPlace = defaultInPlace;
    this.isImpostor = isImpostor;
    this.calendarEra = calendarEra;
    this.media = media;
    this.id = uuid.v4();
  }
}

export default {
  name: "TimelineToolBuilder",
  props: ['vis', 'timeline_details'],
  components: {
    UserSubmission,
    TimelinePoint
  },
  data: function() {
    return {
      notification_bar: false,
      notification_message: "",
      tab: null,
      overlay: false,
      default_point: new Point(
          1,
          "None",
          "None",
          "",
          false,
          false,
          "CE",
          []),
      propsPassed: false,
      submissions_raw: []
    }
  },
  methods: {
    notification(msg) {
      this.notification_message = msg;
      this.notification_bar = true;
    },
    CloseTimelineToolBuilder() {
      this.ResetData();
      this.$emit('close_timeline_builder');
    },
    SaveTool() {
      this.overlay = true;
      if(this.SaveDataValidation()) {
        let user = firebase.auth().currentUser;
        firebase.firestore().collection('users').doc(user.uid).collection('tools')
            .add(JSON.parse(JSON.stringify(this.timeline)))
            .then((data) => {
              // Success
              this.notification("Successfully added tool!");
              this.ResetData();
              this.CloseTimelineToolBuilder();
              this.$emit('reload_dashboard');
              this.GenerateURL(data);
            })
            .catch(err => {
              this.notification('Something went wrong! Check the console for more information.');
              console.error(err);
            });
        this.overlay = false;
      } else {
        this.notification("A title and at least 2 points are required! Please make sure there are no points with a year of '0'.");
        this.overlay = false;
      }
    },
    CopyURL() {
      let text = document.getElementById('timeline_url');
      text.select();
      console.log(text.value);
      navigator.clipboard.writeText(text.value);
      this.notification("Copied to Clipboard!");
    },
    GenerateURL(data) {
      let user = firebase.auth().currentUser;
      firebase.firestore().collection('users').doc(user.uid).collection('tools')
          .doc(data.id).update({"url": `https://www.teachertoolbox.tk/${user.uid}/timeline/${data.id}`});
    },
    UpdateTool() {
      this.overlay = true;
      if(this.SaveDataValidation()) {
        let user = firebase.auth().currentUser;
        firebase.firestore().collection('users').doc(user.uid).collection('tools')
            .doc(this.timeline_details.doc_Id).update(JSON.parse(JSON.stringify(this.timeline)))
            .then(() => {
              // Success
              this.notification("Successfully updated tool!");
              this.CloseTimelineToolBuilder();
            })
            .catch((err) => {
              this.notification('Something went wrong! Check the console for more information.');
              console.error(err);
            });
        this.overlay = false;
      } else {
        this.notification('A title and at least 2 points are required!');
        this.overlay = false;
      }
    },
    RemovePoint(point) {
      let i = this.timeline.points.indexOf(point);
      if (i !== -1) {
        this.timeline.points.splice(i, 1);
      }
      if(this.timeline.points.length === 0) { this.AddNewPoint() }
    },
    AddNewPoint() {
      this.timeline.points.push(new Point(
          this.default_point.year,
          this.default_point.month,
          this.default_point.day,
          this.default_point.content,
          this.default_point.defaultInPlace,
          this.default_point.isImpostor,
          this.default_point.calendarEra,
          []));
    },
    ResetData() {

    },
    SaveDataValidation() {
      if(this.timeline.title.length <= 0 || this.timeline.points.length <= 0) {
        return false;
      }
      for (let i = 0; i < this.timeline.points.length; i++) {
        if(this.timeline.points[i].year === 0) return false;
      }
      return true;
    }
  },
  computed: {
    timeline: function() {
      return this.timeline_details.timeline;
    },
    submissions: function() {
      return this.submissions_raw;
    }
  },
  beforeMount() {
    if(this.timeline.points.length <= 0) {
      this.AddNewPoint();
    }
    firebase.firestore().collection('users').doc(firebase.auth().currentUser.uid)
        .collection('tools').doc(this.timeline_details.doc_Id)
        .collection('submissions').get()
        .then((query) => {
          query.forEach((sub) => {
            this.submissions_raw.push(sub.data());
          });
        });
  }
}
</script>

<style scoped>
.point {
  border-color: #984447;
}
.divider {
  border: 2px solid;
  border-radius: 10px;
}
</style>

