<template>
  <div>
    <v-card @click.stop="OpenTimelineToolBuilder">
      <v-row cols="12">
        <v-col xs="8" sm="8" md="8" lg="8">
          <v-card-title>Timeline Tool</v-card-title>
          <v-card-subtitle>Build a timeline activity that completes when the specified points are in the correct
            order.
          </v-card-subtitle>
        </v-col>
        <v-col xs="4" sm="4" md="4" lg="4">
          <v-img src="@/assets/timeline-clipart.png" position="right"></v-img>
        </v-col>
      </v-row>
    </v-card>
    <timeline-tool-builder :vis="timeline_tool_builder"
                           :timeline_details="{isNew: true, timeline: new_timeline, doc_Id: null}"
                           @close_timeline_builder="timeline_tool_builder = false"
                           @reload_dashboard="ReloadDashboard"
    ></timeline-tool-builder>
  </div>
</template>

<script>
import TimelineToolBuilder from "@/components/tools/TimelineToolBuilder/TimelineToolBuilder";

class Timeline {
  constructor(title, points, options={}) {
    this.title = title;
    this.points = points;
    this.type = 'timeline';
    this.url = '';

    this.options = {
      allowSubmissions: false,
      afterSubmission: 'None'
    };
    this.assignOptions(options);
  }
  assignOptions() {

  }
}

export default {
  components: {TimelineToolBuilder},
  name: "TimelineToolBuilderCard",
  props: ['user'],
  data: function () {
    return {
      new_timeline: new Timeline('', [], {}),
      timeline_tool_builder: false,
    }
  },
  computed: {

  },
  methods: {
    OpenTimelineToolBuilder() {
      this.timeline_tool_builder = true;
      this.$emit("close_picker");
    },
    CloseTimelineToolBuilder() {
      this.timeline_tool_builder = false;
      this.resetBuilderData();
    },
    resetBuilderData() {
      this.timeline_points = [];
      this.AddNewPoint();
      this.tool_title = "";
    },
    async ValidateData() {
      if(this.timeline_points.length < 2) {
        this.$emit('notification_message', 'Timelines must have at least two points.');
        return false;
      }
      if(this.tool_title.length <= 0) {
        this.$emit('notification_message', 'Please include a title.');
        return false;
      }
      return true;
    },
    ReloadDashboard() {
      this.$emit('reload_dashboard');
    }
  }
}
</script>

