<template>
  <div>
    <v-card v-if="loading" @click.stop="LoadingClick" elevation="0" color="secondary" id="loading" max-width="250" class="">
      <v-progress-circular id="loading_circle" :size="100" :width="10" color="white" indeterminate></v-progress-circular>
    </v-card>
    <v-card v-else @click.stop="OpenToolPicker" elevation="0" color="secondary" id="new-tool" max-width="250" class="">
      <v-card-title id="new-tool-title" class="justify-center pt-5">Build Tool</v-card-title>
      <v-icon id="new-tool-icon" color="white" size="150">mdi-hammer-screwdriver</v-icon>
    </v-card>
    <v-dialog id="tool_picker" v-model="tool_picker" max-width="500" scrollable>
      <v-card>
        <v-card-title id="new-tool-dialog-title" class="white--text text-h5">
          Pick a Tool to Build!
        </v-card-title>
        <v-divider></v-divider>
        <v-card-text class="pa-5">
          <div v-for="(tool, index) in available_tools" :key="index">
            <component v-bind:is="tool.component" :tool-data="undefined" :user="user" @notification_message="notification" @reload_dashboard="ReloadDashboard" @close_picker="tool_picker = false"></component>
          </div>
        </v-card-text>
      </v-card>
    </v-dialog>
  </div>

</template>

<script>
import TimelineToolBuilderCard from './tools/TimelineToolBuilder/TimelineToolBuilderCard.vue';

export default {
  name: 'NewTool',
  props: ['user', 'loading'],
  data: function () {
    return {
      tool_picker: false,
      available_tools: [
        {
          "component": TimelineToolBuilderCard
        }
      ]
    }
  },
  methods: {
    OpenToolPicker() {
      this.tool_picker = true;
    },
    notification(msg) {
      this.$emit('notification_message', msg);
    },
    ReloadDashboard() {
      this.$emit('reload_dashboard');
    },
    LoadingClick() {
      this.$emit('notification_message', 'Loading, please wait...');
    }
  }
}
</script>

<style scoped>
#loading {
  width: 250px;
  height: 300px;
  border-radius: 25px;
}

#loading_circle {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

#new-tool-dialog-title {
  background-color: #984447;
}

#new-tool-title {
  color: white;
  font-size: 35px;
  transition: 0.5s;
  opacity: 0;
}

#new-tool-icon {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

#new-tool {
  width: 250px;
  height: 300px;
  border-radius: 25px;
  transition: 0.5s;
  opacity: 0.5;
}

#new-tool:before {
  background: none;
}

#new-tool:hover {
  opacity: 1;
}

#new-tool:hover #new-tool-title {
  opacity: 1;
}
</style>